import Layout from '../../Components/Layout';
import NavbarPadrao from '../../Components/NavbarPadrao';
import CardCreateChar from '../../Components/Card-Create-Char';
import './CriacaoChar.css';
import React, { useState, useEffect } from 'react';
import { nomeService } from '../../Services/NomeService.js';
import Select from 'react-select'; // Importe o React Select
import './CriacaoChar.css';


const CriacaoChar = () => {
    const [genero, setGenero] = useState('');
    const [nomes, setNomes] = useState([]);
    const [nomeSelecionado, setNomeSelecionado] = useState(null);
    const [sobrenomes, setSobrenomes] = useState([]);
    const [sobrenomeSelecionado, setSobrenomeSelecionado] = useState(null);

    useEffect(() => {
        const fetchNomes = async () => {

            if (genero === '1') {
                const nomesFemininos = await nomeService.getFemNames();
                setNomes(nomesFemininos);
            } else if (genero === '2') {
                const nomesMasculinos = await nomeService.getMaleNames();
                setNomes(nomesMasculinos);
            } else {
                setNomes([]);
            }
        };

        const fetchSobrenomes = async () => {
            const sobrenomes = await nomeService.getSurnames();
            setSobrenomes(sobrenomes);
        };

        fetchNomes();
        fetchSobrenomes();
    }, [genero]);

    return (
        <div className='create-char'>
            <Layout>
                <NavbarPadrao />
                <CardCreateChar
                    title="Criao do seu personagem"
                    content={
                        <div className='p-3'>
                            <p> aqui onde tudo se inicia, onde voc dir em qual regio voc deseja que seu personagem nasa, seu nome, algumas histrias de seu passado e se ele ser um Mago ou no. Mas tome cuidado, essa deciso  muito importante. Pense bem na hora de escolher. As decises tomadas aqui s sero reversveis futuramente por meio da aquisio de Arcanics.</p>
                            <p>Antes de comearmos, leia novamente a nossa poltica de segurana e termos do jogo.  l que deixaremos bem claro quais so os seus direitos entre outras informaes sensveis.</p>
                            <a href="#">Poltica de segurana e termos do jogo</a>
                        </div>
                    }
                />

                <CardCreateChar
                    title="Vamos comear por voc"
                    content={
                        <form className='p-3'>
                            <h5 className='fw-bold'>Qual  o seu gnero?</h5>
                            <select
                                className="form-select w-100 w-md-25"
                                aria-label="Selecione um genero"
                                value={genero}
                                onChange={(e) => setGenero(e.target.value)}
                            >
                                <option value="">Selecione um gnero</option>
                                <option value="1">Feminino</option>
                                <option value="2">Masculino</option>
                            </select>

                            <h5 className='fw-bold mt-3'>Qual ser seu nome?</h5>
                            <p>Temos uma vasta lista de nomes e sobrenomes disponveis. Escolha atentamente.</p>
                            <p>Para selecionar um nome, voc antes deve escolher seu gnero.</p>
                            <div className='d-flex flex-column flex-md-row'>
                                <Select
                                    options={nomes}
                                    onChange={setNomeSelecionado}
                                    value={nomeSelecionado}
                                    className="w-100 w-md-50 pe-md-3 mb-3 mb-md-0"
                                    placeholder="Procure por um nome"
                                    isDisabled={!genero}
                                />
                                <Select
                                    options={sobrenomes}
                                    onChange={setSobrenomeSelecionado}
                                    value={sobrenomeSelecionado}
                                    className="w-100 w-md-50"
                                    placeholder="Procure por um sobrenome"
                                />
                            </div>

                            <h5 className='fw-bold mt-3'>Polo de nascimento</h5>
                            <p>Temos dois polos, cada uma com caracteristicas distintas. A escolha no ir influenciar diretamente no desenvolvimento do seu personagem.  uma escolha totalmente para o seu RP.  </p>
                        </form>
                    }
                />
            </Layout>
        </div>
    );
};

export default CriacaoChar;
